// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 15) * 1px;
	// font-size: $sizeValue * 1rem;
	@include mq-md {
		font-size: ($sizeValue * 16) * 1px;
	}
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

// Clearfix
@mixin clearfix() {
	content: "";
	display: table;
	table-layout: fixed;
	clear: both;
}

// Clear after (not all clearfix need this also)
@mixin clearfix-after() {
	clear: both;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
	width: map-get( $columns, $numberColumns ) - ( ( $columns__margin * ( $numberColumns - 1 ) ) / $numberColumns );
}

//Media Queries
@mixin mq-xs($val: $sm-width){
    @media only screen and (max-width: #{$val - 1}) {
        @content;
    }
}

//Media Queries
@mixin mq-sm($val: $sm-width){
    @media only screen and (min-width: #{$val}) {
        @content;
    }
}

//Media Queries
@mixin mq-md($val: $md-width){
    @media only screen and (min-width: #{$val}) {
        @content;
    }
}

//Media Queries
@mixin mq-lg($val: $lg-width) {
    @media only screen and (min-width: #{$val}) {
        @content;
    }
}

//Media Queries
@mixin mq-xl($val: $xl-width) {
    @media only screen and (min-width: #{$val}) {
        @content;
    }
}

//mixin
// https://hirakublog.com/code/342/
//class
@function is-inside($target) {
  @return if("#{selector-replace(&, $target, ".replaced")}" != "#{&}", true, false);
}

//
//.hoge .hoge__box  .hoge__box 
@function parent($value) {
  $selector: $value;
  $array: "";
  $space: if(str-index($selector, " "), str-index($selector, " "), 0);//0

  //
  @while $space > 0 {
    $selector: str-insert($selector, ',', $space);//,
    $array: selector-parse($selector);//
    //  // $l: length($array);
    $selector: #{nth($array, length($array))};//
    $space: if(str-index($selector, " "), str-index($selector, " "), 0);//0
  }
  @return "#{$selector}";
}

@mixin state ($target, $state) {
  //
  @if is-inside($target) {
    @at-root #{selector-replace(&, $target, $target + $state)} {
      @content
    }
  } @else {
    @if $target == parent(#{&})  {
      @at-root #{selector-replace(&, parent(#{&}), $target + $state)} {
        @content
      }
    } @else {
      @at-root #{selector-replace(&, parent(#{&}), $target + $state + " " + parent(#{&}))} {
        @content
      }
    }
  }
}
